@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Golos+Text:wght@400..900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Golos Text", sans-serif;
  color: #1c3749;
  background-color: #ebf1f8;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

button {
  font-family: "Golos Text", sans-serif;
  cursor: pointer;
  outline: none;
  border: none;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 10px;
  width: 100%;
}

.w60 {
  width: 60%;
}
@media (max-width: 991px) {
  .w60 {
    width: 50%;
  }
}
@media (max-width: 768px) {
  .w60 {
    width: 100%;
  }
}

.w40 {
  width: 40%;
}
@media (max-width: 991px) {
  .w40 {
    width: 50%;
  }
}
@media (max-width: 768px) {
  .w40 {
    width: 100%;
  }
}

.w50 {
  width: 50%;
}
@media (max-width: 768px) {
  .w50 {
    width: 100%;
  }
}

.w45 {
  width: 45%;
}

.w55 {
  width: 55%;
}

.w33 {
  width: 33.333%;
}

.w25 {
  width: 25%;
}

a {
  text-decoration: none;
  color: inherit;
}

.btn-min {
  padding: 11px 20px;
  font-size: 14px;
  font-weight: 400;
}

.btn-primary {
  color: #fff;
  background: #2a5cd9;
}
.btn-primary:hover {
  background: #fff;
  color: #161618;
}

.btn-black {
  background: #161618;
  color: #fff;
}
.btn-black:hover {
  background: #2a5cd9;
}

.btn-bord {
  border: 1px solid #fff;
  background: transparent;
  color: #fff;
}
.btn-bord:hover {
  background: #fff;
  color: #161618;
}

.btn-center {
  margin-top: 40px;
  text-align: center;
}

ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: #161618;
  margin-bottom: 30px;
}

li {
  display: flex;
  align-items: center;
  gap: 10px;
}
li::before {
  content: url("./img/check-2.svg");
}

.title {
  font-weight: 500;
  font-size: 44px;
  line-height: 102%;
  letter-spacing: -0.02em;
  color: #161618;
}
@media screen and (max-width: 460px) {
  .title {
    font-size: 30px;
  }
}

.header-container {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 14px 0;
}

.header-items {
  display: flex;
  gap: 40px;
}
.header-items a {
  font-size: 16px;
  cursor: pointer;
  font-weight: 500;
  color: #161618;
}

.header-button {
  display: block;
  width: 193px;
  cursor: pointer;
  height: 38px;
  background-color: #161618;
  color: #fff;
  font-size: 14px;
  text-align: center;
  border-radius: 5px;
  text-decoration: none;
  border: 0;
}

.header-button:hover {
  background-color: #333;
}

.header-burger {
  display: none;
  height: 34px;
  width: 34px;
}

.logo {
  width: 147px;
  height: 49px;
}

.start {
  margin-bottom: 20px;
}

.start-container {
  background-color: #161618;
  background-image: url("./img/start-image.png");
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: right;
  color: white;
  padding: 60px;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
}

.start-badges {
  display: flex;
  gap: 10px;
  margin-bottom: 50px;
}

.start-badges span {
  font-size: 16px;
  padding: 16px;
  border: 1px solid white;
  border-radius: 30px;
}

.start-text {
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 20px;
}

.start-heading {
  font-size: 58px;
  font-weight: 600;
  max-width: 630px;
  line-height: 92%;
  margin-bottom: 35px;
}

.start-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 40px;
  max-width: 600px;
}

.start-list li {
  color: #a6a6a6;
  font-weight: 400;
  padding-left: 27px;
  position: relative;
}

.start-list li::before {
  content: "";
  position: absolute;
  left: 0;
  width: 17px;
  height: 17px;
  background-image: url("./img/check.svg");
}

.start-buttons {
  display: flex;
  gap: 20px;
}

.start-buttons button {
  cursor: pointer;
  border: 1px solid white;
  padding: 20px 30px;
  font-size: 18px;
  color: white;
  background: 0;
  border-radius: 10px;
  font-weight: 600;
}

.start-buttons-blue {
  background-color: #2a5cd9 !important;
  border: 0 !important;
}

.icon-list-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 12px;
}
@media screen and (max-width: 758px) {
  .icon-list-list {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (max-width: 460px) {
  .icon-list-list {
    grid-template-columns: 1fr;
  }
}

.icon-list-item {
  background-color: #fff;
  border-radius: 28px;
  width: 100%;
  padding: 20px;
}

.icon-list-item div {
  display: flex;
  width: 100%;
  margin-bottom: 12px;
  align-items: center;
  gap: 12px;
}

.icon-list-item div h2 {
  font-size: 16px;
  font-weight: 600;
}

.icon-list-item p {
  font-size: 12px;
  font-weight: 400;
  letter-spacing: -2%;
  line-height: 126%;
  max-width: 290px;
  padding-bottom: 25px;
}

.start-image {
  display: none;
}

.about-coach {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 100px;
}
@media screen and (max-width: 460px) {
  .about-coach {
    margin-top: 40px;
  }
}

.about-coach-top,
.about-coach-bottom {
  display: flex;
  gap: 12px;
  align-items: stretch;
}

@media screen and (max-width: 1000px) {
  .about-coach-top {
    flex-direction: column;
  }
  .about-coach-bottom {
    flex-direction: column-reverse;
  }
}
.about-coach-top {
  margin-bottom: 20px;
}
.about-coach-top-image-wrapper {
  min-width: 578px;
  border-radius: 45px;
  overflow: hidden;
}
@media screen and (max-width: 460px) {
  .about-coach-top-image-wrapper {
    min-width: 100%;
    width: 100%;
    height: 266px;
    border-radius: 18px;
  }
  .about-coach-top-image-wrapper img {
    width: 100%;
    object-fit: contain;
    object-position: center;
  }
}
.about-coach-top img {
  width: 100%;
}
.about-coach-top-text {
  background: white;
  border-radius: 45px;
  padding: 40px;
}
@media screen and (max-width: 1000px) {
  .about-coach-top-text {
    padding: 20px;
    border-radius: 18px;
  }
}
.about-coach-top-text h1 {
  color: #161618;
  font-size: 44px;
  font-weight: 500;
  margin-bottom: 40px;
}
@media screen and (max-width: 1000px) {
  .about-coach-top-text h1 {
    font-size: 30px;
    margin-bottom: 20px;
  }
}

.about-coach-top-text p,
.about-coach-bottom-text p {
  font-size: 18px;
  margin-bottom: 20px;
  font-weight: 400;
  line-height: 130%;
}
.about-coach-top-text p:last-child,
.about-coach-bottom-text p:last-child {
  margin: 0;
}
@media screen and (max-width: 460px) {
  .about-coach-top-text p,
  .about-coach-bottom-text p {
    font-size: 14px;
    margin-bottom: 10px;
  }
}

.about-coach-bottom-text {
  padding: 40px;
  background-color: #4c77aa;
  border-radius: 45px;
}
@media screen and (max-width: 460px) {
  .about-coach-bottom-text {
    border-radius: 18px;
    padding: 20px;
  }
}
.about-coach-bottom-text h1 {
  font-size: 44px;
  color: white;
  line-height: 102%;
  font-weight: 500;
  margin-bottom: 20px;
}
@media screen and (max-width: 460px) {
  .about-coach-bottom-text h1 {
    font-size: 30px;
    margin-bottom: 20px;
  }
}
.about-coach-bottom-text p {
  color: white;
  font-weight: 400;
}

.about-coach-bottom-image-wrapper {
  border-radius: 45px;
  min-width: 578px;
  height: 346px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
}
@media screen and (max-width: 768px) {
  .about-coach-bottom-image-wrapper {
    max-width: 100%;
    height: auto;
    min-width: 100%;
    border-radius: 18px;
  }
  .about-coach-bottom-image-wrapper img {
    width: 70%;
    object-fit: contain;
  }
}

.base-list {
  margin-top: 100px;
}
.base-list-wrapper {
  max-width: 1396px;
  margin: 0 auto;
  background: white;
  padding: 66px 71px;
  border-radius: 50px;
  display: flex;
  flex-direction: column;
}
@media screen and (max-width: 460px) {
  .base-list {
    margin-top: 50px;
    padding: 20px;
    border-radius: 18px;
  }
}
@media screen and (max-width: 460px) {
  .base-list-medium-reverse {
    flex-direction: column-reverse !important;
  }
}

.base-list-heading {
  font-size: 44px;
  font-weight: 500;
  margin-bottom: 50px;
}
@media screen and (max-width: 460px) {
  .base-list-heading {
    font-size: 30px;
    margin-bottom: 30px;
  }
}

.base-list-container {
  display: flex;
  flex-direction: column;
  gap: 35px;
}

.base-list-container > div {
  display: flex;
  align-items: center;
  gap: 50px;
}
@media screen and (max-width: 460px) {
  .base-list-container > div {
    flex-direction: column;
    gap: 20px;
  }
}

.base-list-container > div > div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.base-list-container > div > div > h2 {
  font-size: 33px;
  line-height: 126%;
  font-weight: 500;
  margin-bottom: 20px;
}
@media screen and (max-width: 460px) {
  .base-list-container > div > div > h2 {
    font-size: 20px;
    margin-bottom: 14px;
  }
}

.base-list-container > div > div > p {
  line-height: 150%;
  font-size: 18px;
  font-weight: 400;
  color: #161618;
  opacity: 60%;
}
@media screen and (max-width: 460px) {
  .base-list-container > div > div > p {
    font-size: 14px;
    line-height: 126%;
    letter-spacing: -0.01em;
  }
}

.base-list-container > div > img {
  border-radius: 45px;
}
@media screen and (max-width: 460px) {
  .base-list-container > div > img {
    width: 100%;
    border-radius: 18px;
    height: 260px;
    object-fit: cover;
  }
}

.base-list-button {
  display: inline-block;
  margin: 60px auto;
  margin-bottom: 0;
  padding: 16px 30px;
  background-color: #20222c;
  color: white;
  border-radius: 10px;
  font-size: 20px;
  cursor: pointer;
}
.base-list-button-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}
@media screen and (max-width: 460px) {
  .base-list-button {
    width: 100%;
    margin-top: 20px;
    font-size: 15px;
  }
}

.base-list-listing {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 14px;
  line-height: 130%;
  color: #161618;
}

.how-much {
  margin-top: 100px;
}
@media screen and (max-width: 460px) {
  .how-much {
    margin-top: 40px;
    padding: 0 14px;
  }
}
.how-much-wrapper {
  background: #161618;
  border-radius: 45px;
  padding: 45px 40px;
}
@media screen and (max-width: 460px) {
  .how-much-wrapper {
    border-radius: 18px;
    padding: 20px;
  }
}
.how-much-content {
  display: flex;
  gap: 41px;
  color: white;
}
@media screen and (max-width: 768px) {
  .how-much-content {
    flex-direction: column;
    gap: 20px;
  }
}
.how-much-left {
  max-width: 577px;
}
.how-much-left h2 {
  font-size: 44px;
  font-weight: 500;
  margin-bottom: 40px;
}
@media screen and (max-width: 460px) {
  .how-much-left h2 {
    font-size: 30px;
  }
}
.how-much-left-text {
  font-size: 16px;
  color: #eaeaea;
  line-height: 126%;
  font-weight: 400;
  margin-bottom: 20px;
}
@media screen and (max-width: 460px) {
  .how-much-left-text {
    font-size: 14px;
  }
}
.how-much-left-bold {
  font-weight: 500;
  font-size: 23px;
  line-height: 126%;
  margin-bottom: 40px;
}
@media screen and (max-width: 460px) {
  .how-much-left-bold {
    font-size: 18px;
  }
}
.how-much-left button {
  padding: 19px 54px;
  background: #2a5cd9;
  color: white;
  font-size: 18px;
  font-weight: 600;
  border-radius: 10px;
  outline: none;
  border: none;
  cursor: pointer;
}
@media screen and (max-width: 460px) {
  .how-much-left button {
    width: 100%;
    max-width: 100%;
  }
}
.how-much-bold {
  font-size: 23px;
  line-height: 126%;
  font-weight: 500;
  margin-bottom: 40px;
}
.how-much-right {
  background-color: #282f3f;
  background-image: url("./img/russian-ruble.png");
  background-position: right;
  background-repeat: no-repeat;
  border-radius: 28px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 396px;
}
@media screen and (max-width: 460px) {
  .how-much-right {
    border-radius: 18px;
    padding: 10px;
    background-image: url("./img/russian-ruble-mb.png");
    background-position: center;
  }
}
.how-much-right-lines {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.how-much-right-line {
  background: #576074;
  border-radius: 28px;
  padding: 5px;
  display: flex;
  align-items: center;
  gap: 14px;
}
.how-much-right-line span {
  background-color: white;
  font-weight: 500;
  font-size: 16px;
  color: #161618;
  text-align: center;
  display: flex;
  align-items: center;
  height: 39px;
  border-radius: 100px;
  padding: 0 15px;
}
@media screen and (max-width: 460px) {
  .how-much-right-line span {
    font-size: 10px;
    height: 27px;
  }
}
.how-much-right-line p {
  font-weight: 400;
  font-size: 14px;
  max-width: 230px;
}
@media screen and (max-width: 460px) {
  .how-much-right-line p {
    font-size: 8px;
    max-width: 40%;
  }
}
.how-much-right-bottom-price {
  font-weight: 400;
  font-size: 44px;
}
@media screen and (max-width: 460px) {
  .how-much-right-bottom-price {
    font-size: 22px;
  }
}
.how-much-right-bottom-sum {
  font-weight: 400;
  font-size: 82px;
}
@media screen and (max-width: 460px) {
  .how-much-right-bottom-sum {
    font-size: 55px;
  }
}
.how-much-right-bottom p {
  font-weight: 400;
  font-size: 18px;
  letter-spacing: -0.01em;
  color: #808aa0;
  margin-top: 20px;
}
@media screen and (max-width: 460px) {
  .how-much-right-bottom p {
    font-size: 13px;
    margin-top: 0px;
  }
}

@media screen and (max-width: 1000px) {
  .container {
    padding: 10px;
  }
  .header-burger {
    display: block;
  }
  .header-container ul,
  .header-items,
  .header-items li {
    display: none;
  }
  .header-button {
    display: none;
  }
  .start-badges {
    justify-content: center;
  }
  .start-badges span {
    font-size: 12px;
    padding: 12px 16px;
  }
  .start-container {
    padding: 14px 20px;
  }
  .start-text {
    font-size: 18px;
    text-align: center;
  }
  .start-container,
  .start {
    background-image: none;
  }
  .start-heading {
    font-size: 30px;
    text-align: center;
  }
  .start-buttons {
    margin-bottom: 10px;
  }
  .start-list li {
    font-size: 14px;
  }
  .start-buttons {
    flex-direction: column;
    width: 100%;
  }
  .start-buttons button {
    max-width: 100%;
  }
  .icon-list {
    flex-direction: column;
  }
  .icon-list-item {
    padding-bottom: 0;
  }
  .start-image {
    display: block;
  }
}
.swiper {
